@mixin st_btn() {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;

	cursor: pointer;

	&:hover, &:focus {
		text-decoration: none;
	}

}

//color extends
%blue_btn_1 {
	font-weight: 700;
	color: $white;
	background-color: $color_link_base;
	transition: background-color .3s ease;

	@include st_btn();

	&:hover {
		background-color: darken($color_link_base, 10%);
	}

}

//size extends
%btn_size_1 {
	height: 4rem;
	padding: 0 1.2rem;

	line-height: 4rem;
	font-size: 1.6rem;

	border-radius: $gl_radius;
}

.footer_form__btn_submit {
	padding: 1rem 3rem;
	color: $white;
	border: 1px solid $white;
	cursor: pointer;
	transition: .3s;
	
	@include st_btn();

	@include bp($point_3,min) {
		&:hover, &:focus {
			background-color: rgba($white, 0.4);
		}
	}
}