.form_radio__label:after, .form_checkbox__label:after {
  transition: opacity 0.3s ease;
  will-change: opacity;
}

.form_select__label, .form_textarea__label, .form_input__label {
  display: block;
  margin-bottom: 0.4rem;
  font-size: 1.6rem;
  color: #000;
}

.form_select__element, .form_textarea__element, .form_input__element {
  display: block;
  width: 100%;
  height: 4rem;
  padding: 0 2rem;
  font-size: 1.6rem;
  font-family: "RobotoCondensed", sans-serif;
  color: #000;
  box-shadow: inset 0 0 0 1px #ccc;
  border: none;
  border-radius: 0.5rem;
}
.form_select__element::-webkit-input-placeholder, .form_textarea__element::-webkit-input-placeholder, .form_input__element::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
}
.form_select__element::-moz-placeholder, .form_textarea__element::-moz-placeholder, .form_input__element::-moz-placeholder {
  color: rgba(0, 0, 0, 0.4);
}
.form_select__element:-ms-input-placeholder, .form_textarea__element:-ms-input-placeholder, .form_input__element:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
}
.form_select__element:hover, .form_textarea__element:hover, .form_input__element:hover, .form_select__element:focus, .form_textarea__element:focus, .form_input__element:focus {
  box-shadow: inset 0 0 0 1px #000;
}

.footer_form__input {
  margin-right: 2rem;
}

.footer_form__input_email {
  display: block;
  width: 100%;
  max-width: 22rem;
  padding: 1.4rem 1rem;
  color: #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.4);
}
.footer_form__input_email::-webkit-input-placeholder {
  color: #fff;
}
.footer_form__input_email::-moz-placeholder {
  color: #fff;
}
.footer_form__input_email:-ms-input-placeholder {
  color: #fff;
}
.footer_form__input_email:focus {
  border: 1px solid #fff;
}

.form_checkbox__label {
  position: relative;
  padding-left: 3rem;
  font-size: 1.6rem;
  color: #000;
  cursor: pointer;
}
.form_checkbox__label:before, .form_checkbox__label:after {
  position: absolute;
  top: -1px;
  left: 0;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.form_checkbox__label:before {
  content: "";
  border-radius: 2px;
  border: 1px solid #000;
}
.form_checkbox__label:after {
  text-align: center;
  line-height: 2rem;
  opacity: 0;
  color: #000;
}
.form_checkbox__element:checked + .form_checkbox__label:after {
  opacity: 1;
}

.form_radio__label {
  position: relative;
  padding-left: 3rem;
  font-size: 1.6rem;
  color: #000;
  cursor: pointer;
}
.form_radio__label:before {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  border: 1px solid #000;
  cursor: pointer;
}
.form_radio__label:after {
  content: "";
  position: absolute;
  top: 0.4rem;
  left: 0.5rem;
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  background: #000;
  opacity: 0;
}
.form_radio__element:checked + .form_radio__label:after {
  opacity: 1;
}

