:root {
  --height-header: 8rem;
  --gl-indent: 1.5rem;
}

html {
  height: 100%;
  font-family: "ApercuPro";
}
@media screen and (min-width: 640px) {
  html {
    font-size: 10px;
  }
}
@media screen and (max-width: 640px) {
  html {
    font-size: 9px;
  }
}

body {
  position: relative;
  display: flex;
  height: 100%;
  font-size: 1.6rem;
  color: #19202C;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}
@supports (display: grid) {
  body {
    display: block;
  }
}

.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  height: auto !important;
  overflow-x: hidden;
}

.base {
  width: 100%;
  position: relative;
  padding: var(--height-header) 0 0;
  flex: 1 0 auto;
}

a {
  text-decoration: none;
}

.elements_list {
  padding: 3rem;
}

.cover_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 7.6rem;
  height: var(--height-header);
  width: 100%;
  padding: var(--gl-indent);
  font-size: 1rem;
  will-change: transform;
  z-index: 100;
}

.header_menu {
  transition: 0.3s ease-in-out;
}
@media screen and (max-width: 1023px) {
  .header_menu {
    position: absolute;
    top: 0;
    right: 0;
    flex-direction: column;
    padding: 2.4rem;
    background-color: #F4F4F4;
    z-index: 101;
  }
}
@media screen and (max-width: 1023px) {
  .header_menu.header_menu--hide {
    transform: translateY(-100%);
  }
}

.header_logo {
  flex-grow: 1;
  max-width: 13.4rem;
}

@media screen and (max-width: 1023px) {
  .header_nav__list {
    flex-direction: column;
  }
}

@media screen and (min-width: 1023px) {
  .header_nav__item {
    margin-right: 2.4rem;
  }
}
@media screen and (max-width: 1023px) {
  .header_nav__item {
    margin: 0.4rem 0;
  }
}
.header_nav__item:last-child {
  margin-right: 0;
}

.header_nav__link {
  position: relative;
  font-size: 1.2rem;
  font-family: "ApercuPro";
  text-transform: uppercase;
  text-decoration: none;
  color: #19202C;
  transition: 0.3s;
}
@media screen and (min-width: 1023px) {
  .header_nav__link:hover::before {
    width: 100%;
  }
}
.header_nav__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: #19202C;
  transition: 0.3s;
}

@media screen and (min-width: 1023px) {
  .header_social {
    margin: 0 2.4rem;
  }
}
@media screen and (max-width: 1023px) {
  .header_social {
    margin: 2.4rem 0;
  }
}

.header_search {
  width: 1.74rem;
  height: 1.74rem;
  margin-right: 2.4rem;
  color: #777777;
}
@media screen and (max-width: 1023px) {
  .header_search {
    display: none;
  }
}

.footer {
  padding: 2rem 1.4rem 10rem 1.4rem;
  background-color: #BD243A;
}

.footer__wrapper {
  display: grid;
}
@media screen and (min-width: 1024px) {
  .footer__wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: flex-start;
    align-items: flex-start;
  }
}
@media screen and (min-width: 480px) {
  .footer__wrapper {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media screen and (max-width: 480px) {
  .footer__wrapper {
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.footer__logo {
  display: block;
  margin-bottom: 10rem;
  width: 13.6rem;
}

@media screen and (max-width: 1024px) {
