  }
}

.footer_column__title,
.footer_nav__item {
  padding-bottom: 0.74rem;
  margin-bottom: 2.64rem;
  font-size: 1.4rem;
  color: #fff;
  text-transform: uppercase;
  border-bottom: 1px solid #fff;
}

.footer_nav__item:last-child {
  margin-bottom: 0;
}

.footer_nav__item .footer_nav__link {
  color: inherit;
  text-decoration: none;
  transition: 0.3s;
}
@media screen and (min-width: 1023px) {
  .footer_nav__item .footer_nav__link:hover {
    color: #b9b9b9;
  }
