}
@media (max-width: 1024px) and (min-width: 480px) {
  .footer_column:last-child {
    grid-column: span 4;
    grid-row: span 1;
    width: 100%;
  }
}
@media screen and (max-width: 480px) {
  .footer_column:last-child {
    grid-column: span 3;
    grid-row: span 1;
    width: 100%;
  }
}
@media (max-width: 1024px) and (min-width: 480px) {
  .footer_column {
    grid-column: span 1;
  }
}
@media screen and (max-width: 480px) {
  .footer_column {
    grid-column: span 1;
  }
  .footer_column:first-child {
    grid-column: span 3;
  }
}

.footer_social {
  margin-bottom: 1.7rem;
}

.footer__copyright {
  display: inline-flex;
  align-items: center;
  color: #fff;
}
.footer__copyright .footer__copyright__icon {
  margin-right: 0.5rem;
  font-size: 2.4rem;
}

.footer_tag_list__title {
  margin-bottom: 2.64rem;
  font-size: 1.4rem;
  color: #fff;
  text-transform: uppercase;
}

.footer_tag_list__item {
  margin-bottom: 2rem;
}
.footer_tag_list__item:last-child {
  margin-bottom: 0;
}

.footer_tag_list__link {
  font-size: 1.4rem;
  color: #fff;
  text-decoration: none;
  transition: 0.3s;
}
@media screen and (min-width: 1023px) {
  .footer_tag_list__link:hover {
    color: #b9b9b9;
  }
}

.footer_description {
  margin-bottom: 2rem;
}
.footer_description .footer_description__paragraph {
  display: inline-block;
  margin-bottom: 2rem;
  font-size: 1.2rem;
  color: #fff;
  line-height: 1.6rem;
}
.footer_description .footer_description__paragraph:last-child {
  margin-bottom: 0;
}

.footer_form {
  display: flex;
}

.form_textarea__element {
  padding-top: 2rem;
  padding-bottom: 2rem;
  height: 15rem;
}

.form_checkbox__block {
  display: block;
}

.form_checkbox__element {
  display: none;
}

.form_radio__block {
  display: block;
}

.form_radio__element {
  display: none;
}

.section {
  position: relative;
  width: 100%;
  font-size: 1rem;
  padding: 0 var(--gl-indent);
}
.section.demo_mod {
  height: calc(100vh - (var(--height-footer) + var(--height-header)));
  font-size: 4rem;
  color: #fff;
  text-align: center;
  background-color: #000;
}

.section_in {
  max-width: 116rem;
  width: 100%;
  margin: 0 auto;
}

.social_item {
  margin-right: 2.4rem;
}
.social_item:last-child {
  margin-right: 0;
}

.main_social .icon {
  transition: 0.3s;
}

.social_link {
  display: block;
  color: #777777;
}
.footer_social .social_link {
  color: #fff;
  transition: 0.3s;
}
@media screen and (min-width: 1023px) {
  .footer_social .social_link:hover {
    color: #b9b9b9;
  }
}
@media screen and (min-width: 1023px) {
  .social_link:hover {
    color: #282626;
  }
}
.social_link.social_link--size_1_mod {
  width: 1rem;
  height: 2rem;
}
.social_link.social_link--size_2_mod {
  width: 2.4rem;
  height: 1.94rem;
}
.social_link.social_link--size_3_mod {
  width: 3rem;
  height: 2rem;
}
.social_link.social_link--size_4_mod {
  width: 2rem;
  height: 1.4rem;
}

.lang__item {
  position: relative;
  padding: 0 0.6rem;
  font-size: 1.6rem;
  text-transform: uppercase;
  color: #777777;
  transition: 0.3s;
}
.lang__item:hover {
  color: black;
}
.lang__item::before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  width: 1px;
  height: 80%;
  background-color: #777777;
  transform: translateY(-50%);
}
.lang__item:first-child {
  padding-left: 0;
}
.lang__item:last-child {
  padding-right: 0;
}
.lang__item:last-child::before {
  background-color: transparent;
}

.lang__link {
  color: inherit;
}

.burger {
  position: relative;
  display: inline-block;
  width: 2.6rem;
  z-index: 102;
}
@media screen and (min-width: 640px) {
  .burger {
    height: 1.2rem;
  }
}
@media screen and (min-width: 1024px) {
  .burger {
    display: none;
  }
}
@media screen and (max-width: 640px) {
  .burger {
    height: 1.4rem;
  }
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  width: inherit;
  height: 2px;
  background-color: #19202C;
  transition: 0.3s;
}
@media screen and (min-width: 640px) {
  .burger::before {
    top: 0;
  }
}
@media screen and (max-width: 640px) {
  .burger::before {
    top: 1px;
  }
}
.burger::after {
  bottom: 0;
}
.burger.burger--open .burger_decor {
  opacity: 0;
  transform: translateX(-100%);
}
.burger.burger--open::before {
  top: 5px;
  transform: rotate(45deg);
}
.burger.burger--open::after {
  bottom: 5px;
  transform: rotate(-45deg);
}

.burger_decor {
  position: absolute;
  top: 50%;
  left: 0;
  width: inherit;
  height: 2px;
  transform: translateY(-50%);
  background-color: #19202C;
  transition: 0.3s ease-in-out;
}

.grid_wrapper {
  display: grid;
  grid-template-columns: repeat(5, 21.6rem);
  grid-auto-flow: dense;
  gap: 2rem;
  margin: 0 auto;
  max-width: 116rem;
}
@media (max-width: 1200px) and (min-width: 768px) {
  .grid_wrapper {
    grid-template-columns: repeat(3, 1fr);
